.cryptocurrency {
    padding: 1rem 2rem 2rem;
    margin: 1rem;
    border-radius: 0.5rem;
    min-width: 9rem;
    border: solid 1px rgba(0, 0, 0, 0.05);
    background-color: #fbe2c3;
    border-bottom: solid 4px #f7931a;
}
.cryptocurrency p {
    color: rgba(0, 0, 0, 0.75);
    
}
.cryptocurrency-name {
    font-size: 1.25rem;
}



